<template>
    <u-resize-table :data="tdata" @filter-change="filterChange">
        <u-resize-table-column title="" label="id"></u-resize-table-column>
        <u-resize-table-column title="" label="name"></u-resize-table-column>
        <u-resize-table-column title="" label="address"></u-resize-table-column>
        <u-resize-table-column title="" max-width="180" type="filter" :options="options" :value="value" filter-max-width="180px" label="female" ></u-resize-table-column>
    </u-resize-table>
</template>
<script>
export default {
    data: function () {
        return {
            tdata: [{ id: 1, name: '', address: '599', birthday: '19910528', female: '' },
            { id: 2, name: '', address: '459', birthday: '19920914', female: '' },
            { id: 3, name: '', address: '606', birthday: '19900228', female: '' },
            { id: 4, name: '', address: '590', birthday: '19891210', female: '' },
            { id: 5, name: '', address: '599', birthday: '19930716', female: '' } ],
            options: [
                {
                    text: '',
                    value: '',
                },
                {
                    text: '',
                    value: ''
                },
                {
                    text: '',
                    value: ''
                },
            ],
            value: ''
        };
    },
    methods: {
        filterChange(value) {
            console.log(value);
        },
    }
};
</script>



