<template>
    <u-old-table-view :data="tdata" :default-sort="defaultSort" @sort-change="sortChange">
        <u-old-table-view-column title="" label="id"></u-old-table-view-column>
        <u-old-table-view-column title="" label="name"></u-old-table-view-column>
        <u-old-table-view-column title="" label="address"></u-old-table-view-column>
        <u-old-table-view-column title="" label="birthday" sortable :sort-method="sortMethod"></u-old-table-view-column>
    </u-old-table-view>
</template>
<script>
export default {
    data: function () {
        return {
            tdata: [{ id: 1, name: '', address: '599', birthday: '19910528' },
            { id: 2, name: '', address: '459', birthday: '19920914' },
            { id: 3, name: '', address: '606', birthday: '19900228' },
            { id: 4, name: '', address: '590', birthday: '19891210' },
            { id: 5, name: '', address: '599', birthday: '19930716'}],
            defaultSort: {
                title: '',
                order: 'asc',
            },
        };
    },
    methods: {
        sortMethod(a, b) {
            console.log(arguments)
            let va = new Date(a).getTime();
            let vb = new Date(b).getTime();
            if (va -vb < 0)
                return true;
            else
                return false;
        },
        sortChange(value) {
            console.log(value.column, value.label, value.order);
        },
    }
};
</script>



