<template>
    <u-old-table-view :data="tdata" @filter-change="filterChange">
        <u-old-table-view-column title="" label="date" sortable :formatter="dateFormat"></u-old-table-view-column>
        <u-old-table-view-column title="" label="name" ></u-old-table-view-column>
        <u-old-table-view-column title="" label="address" ></u-old-table-view-column>
        <u-old-table-view-column title="" label="female" filter :options="options" :value="value" :filter-method="filterMethod"></u-old-table-view-column>
    </u-old-table-view>
</template>
<script>
export default {
    data: function () {
        return {
            tdata: [{
                date: 1501977600000,
                name: '',
                address: ' 599',
                female: '',
                use: 12,
                total: 20,
            }, {
                date: 1502236800000,
                name: '',
                address: ' D3',
                female: '',
                use: 12,
                total: 20,
            }, {
                date: 1503100800000,
                name: '',
                address: ' ',
                female: '',
                use: 12,
                total: 20,
            }, {
                date: 1503964800000,
                name: '',
                address: ' ',
                female: '',
                use: 12,
                total: 20,
            }],
            options: [
                {
                    name: '',
                    value: '',
                },
                {
                    name: '',
                    value: ''
                },
                {
                    name: '',
                    value: ''
                },
            ],
            value: ''
        };
    },
    methods: {
        filterMethod(value, columnValue) {
            if (value === '')
                return true;
            return columnValue === value;
        },
        dateFormat(row) {
            const value = row.date;
            const year = new Date(value).getFullYear();
            let month = new Date(value).getMonth() + 1;
            month += '';
            if(month.length === 1)
                month = '0' + month;
            const date = new Date(value).getDate();
            return year + '-' + month + '-' + date;
        },
        filterChange(data){
            console.log(data);
        }
    }
};
</script>



