<template>
    <div>
        <u-checkboxes v-model="checkedList">
            <u-checkbox label="isShow"></u-checkbox>
            <u-checkbox label="isNameShow"></u-checkbox>
            <u-checkbox label="isAddressShow"></u-checkbox>
        </u-checkboxes>
        <u-old-table-view :data="tdata" border max-height="400">
            <u-old-table-view-column :visible="checkedList.includes('isShow')" label="id">
                <div slot="headerTitle">
                    
                </div>
            </u-old-table-view-column>
            <u-old-table-view-column ellipsis :visible="checkedList.includes('isNameShow')" title="" label="name"></u-old-table-view-column>
            <u-old-table-view-column title="" :visible="checkedList.includes('isAddressShow')" label="address" width="400px"></u-old-table-view-column>
        </u-old-table-view>
    </div>
</template>
<script>
export default {
    data: function () {
        return {
            tdata: [{ id: 1, name: '', address: '599', birthday: '19910528' },
            { id: 2, name: '', address: '459', birthday: '19920914' },
            { id: 3, name: '', address: '606', birthday: '19900228' },
            { id: 4, name: '', address: '590', birthday: '19891210' },
            { id: 5, name: '', address: '599', birthday: '19930716' }],
           checkedList: ['isShow', 'isNameShow', 'isAddressShow'],
        };
    },
};
</script>



