<template>
    <div>
        <u-checkboxes v-model="checkedList">
            <u-checkbox label="isShow"></u-checkbox>
            <u-checkbox label="isNameShow"></u-checkbox>
            <u-checkbox label="isAddressShow"></u-checkbox>
            <u-checkbox label="isFemaleShow"></u-checkbox>
        </u-checkboxes>
        <u-resize-table :data="tdata">
            <u-resize-table-column :visible="checkedList.includes('isShow')" title="" label="id"></u-resize-table-column>
            <u-resize-table-column :visible="checkedList.includes('isNameShow')" title="" label="name"></u-resize-table-column>
            <u-resize-table-column :visible="checkedList.includes('isAddressShow')" title="" label="address"></u-resize-table-column>
            <u-resize-table-column :visible="checkedList.includes('isFemaleShow')" title=""  label="birthday" ></u-resize-table-column>
        </u-resize-table>
    </div>
</template>
<script>
export default {
    data: function () {
        return {
            tdata: [{ id: 1, name: '', address: '599', birthday: '19910528' },
            { id: 2, name: '', address: '459', birthday: '19920914' },
            { id: 3, name: '', address: '606', birthday: '19900228' },
            { id: 4, name: '', address: '590', birthday: '19891210' },
            { id: 5, name: '', address: '599', birthday: '19930716' }],
            checkedList: ['isShow', 'isNameShow', 'isAddressShow', 'isFemaleShow'],
        };
    },
};
</script>



