<template>
    <div>
        <u-old-table-view :data="tdata" width="800" border :loading="loading">
            <u-old-table-view-column width="200" fixed="left" title="" label="id"></u-old-table-view-column>
            <u-old-table-view-column title="" width="200" label="name" ></u-old-table-view-column>
            <u-old-table-view-column title="" width="200" label="province"></u-old-table-view-column>
            <u-old-table-view-column title="" width="200" label="city"></u-old-table-view-column>
            <u-old-table-view-column title="" width="200" label="district"></u-old-table-view-column>
            <u-old-table-view-column title="" width="200" label="address"></u-old-table-view-column>
            <u-old-table-view-column title="" width="200" label="address"></u-old-table-view-column>
            <u-old-table-view-column title="" fixed="right" width="150">
                <template slot-scope="scope">
                    <u-button @click="click(scope.row)"></u-button>
                </template>
            </u-old-table-view-column>
            <div slot="no-data-text">
                <span style="margin-right:10px">,</span>
                <u-link></u-link>
            </div>
        </u-old-table-view>
        <u-modal :visible.sync="visible">
            <div>
                <span>{{current.name}}</span>
                <span style="margin-left:10px;">{{current.address}}</span>
            </div>
        </u-modal>
    </div>
</template>
<script>
export default {
    data: function () {
        return {
            tdata: [{ id: 1, name: '', province: '', city: '', district: '', address: '599', birthday: '19910528' },
            { id: 2, name: '', province: '', city: '', district: '', address: '459', birthday: '19920914' },
            { id: 3, name: '', province: '', city: '', district: '', address: '606', birthday: '19900228' },
            { id: 4, name: '', province: '', city: '', district: '', address: '590', birthday: '19891210' },
            { id: 5, name: '', province: '', city: '', district: '', address: '599', birthday: '19930716' },
            { id: 6, name: '', province: '', city: '', district: '', address: '599', birthday: '19910528' },
            { id: 7, name: '', province: '', city: '', district: '', address: '459', birthday: '19920914' },
            { id: 8, name: '', province: '', city: '', district: '', address: '606', birthday: '19900228' },
            { id: 9, name: '', province: '', city: '', district: '', address: '590', birthday: '19891210' },
            { id: 10, name: '', province: '', city: '', district: '', address: '599', birthday: '19930716' }],
            current: {},
            visible: false,
            show: false,
            loading: false,
        };
    },
    methods: {
        click(row) {
            console.log('click');
            console.log(row);
            this.visible = true;
            this.current = row;
        },
    }
};
</script>



