<template>
    <u-resize-table :data="tdata">
        <u-resize-table-column title="" label="id"></u-resize-table-column>
        <u-resize-table-column title="" label="name" :formatter="formatter"></u-resize-table-column>
        <u-resize-table-column title="" label="address"></u-resize-table-column>
        <u-resize-table-column title="" label="birthday" ></u-resize-table-column>
    </u-resize-table>
</template>
<script>
export default {
    data: function () {
        return {
            tdata: [{ id: 1, name: '', address: '599', birthday: '19910528' },
            { id: 2, name: '', address: '459', birthday: '19920914' },
            { id: 3, name: '', address: '606', birthday: '19900228' },
            { id: 4, name: '', address: '590', birthday: '19891210' },
            { id: 5, name: '', address: '599', birthday: '19930716' } ],
        };
    },
    methods: {
        formatter(row, column) {
            if (row.name === '')
                return '';
            else
                return row.name;
        },
    }
};
</script>



