<template>
    <u-resize-table :data="tdata" :default-sort="defaultSort" @sort-change="sortChange">
        <u-resize-table-column title="" type="sortable" label="id"></u-resize-table-column>
        <u-resize-table-column title="" type="sortable" label="name"></u-resize-table-column>
        <u-resize-table-column title="" label="address"></u-resize-table-column>
        <u-resize-table-column title="" label="birthday" ></u-resize-table-column>
    </u-resize-table>
</template>
<script>
export default {
    data: function () {
        return {
            tdata: [{ id: 1, name: '', address: '599', birthday: '19910528' },
            { id: 2, name: '', address: '459', birthday: '19920914' },
            { id: 3, name: '', address: '606', birthday: '19900228' },
            { id: 4, name: '', address: '590', birthday: '19891210' },
            { id: 5, name: '', address: '599', birthday: '19930716' }],
            defaultSort: {
                title: '',
                order: 'desc',
            }
        };
    },
    methods: {
        sortChange(value) {
            console.log(value);
        },
    }
};
</script>



