<template>
    <div>
        <u-old-table-view :data="tdata" max-height="500" data-pattern="normal" pattern="virtual">
            <u-old-table-view-column title="" label="id"></u-old-table-view-column>
            <u-old-table-view-column ellipsis title="" label="name"></u-old-table-view-column>
            <u-old-table-view-column ellipsis title="" label="address"></u-old-table-view-column>
            <u-old-table-view-column title="" label="birthday"></u-old-table-view-column>
        </u-old-table-view>
    </div>
</template>
<script>
export default {
    data() {
        return {
            tdata: [],
        };
    },
    created() {
        const nameMap = ["", "", "", "", ""];
        for(let i=0; i<1000; i++) {
            this.tdata.push({ id: i, name: nameMap[i%5], province: '', city: '', district: '', address: '599', birthday: '19910528' });
        }
    },
};
</script>



