<template>
    <div>
        <u-old-table-view :data="tdata">
            <u-old-table-view-column title="" label="id"></u-old-table-view-column>
            <u-old-table-view-column title="" label="name"></u-old-table-view-column>
            <u-old-table-view-column title="" label="address"></u-old-table-view-column>
            <u-old-table-view-column title="">
                <template slot-scope="{row}">
                    <u-button @click="click(row)"></u-button>
                </template>
            </u-old-table-view-column>
        </u-old-table-view>
        <u-modal :visible.sync="visible">
            <div>
                <span>{{current.name}}</span>
                <span style="margin-left:10px;">{{current.address}}</span>
            </div>
        </u-modal>
    </div>
</template>
<script>
export default {
    data: function () {
        return {
            tdata: [{ id: 1, name: '', address: '599', birthday: '19910528' },
            { id: 2, name: '', address: '459', birthday: '19920914' },
            { id: 3, name: '', address: '606', birthday: '19900228' },
            { id: 4, name: '', address: '590', birthday: '19891210' },
            { id: 5, name: '', address: '599', birthday: '19930716' }],
            current: {},
            visible: false,
        };
    },
    methods: {
        click(row) {
            console.log('click');
            console.log(row);
            this.visible = true;
            this.current = row;
        },
    }
};
</script>



