<template>
    <u-linear-layout direction="vertical">
        <u-old-table-view :data="tdata" @selection-change="selectionChange($event)">
            <u-old-table-view-column type="selection" title="" width="100"></u-old-table-view-column>
            <u-old-table-view-column title="" label="name" ></u-old-table-view-column>
            <u-old-table-view-column title="" label="address" ></u-old-table-view-column>
            <u-old-table-view-column title="" label="birthday"></u-old-table-view-column>
        </u-old-table-view>
    </u-linear-layout>
</template>
<script>
export default {
    data: function () {
        return {
            tdata: [{ id: 1, name: '', address: '599', birthday: '19910528' },
            { id: 2, name: '', address: '459', birthday: '19920914' },
            { id: 3, name: '', address: '606', birthday: '19900228' },
            { id: 4, name: '', address: '590', birthday: '19891210' },
            { id: 5, name: '', address: '599', birthday: '19930716'} ],
            allChecked: false,
            checkedData: [],
        };
    },
    watch: {
        allChecked(newValue) {
            console.log(newValue);
        },
    },
    methods: {
        selectionChange(data) {
            console.log(data);
            this.checkedData = data;
        },
    }
};
</script>



